<template>
    <div>
        <div class="search-con">
            <span></span>
            <i-input v-model="nameValue" placeholder="" style="width: 150px;margin-right: 8px;"></i-input>
            <i-button @click="tableSearch" type="primary" icon="md-search"></i-button>
        </div>
        <Tabs>
            <TabPane label="" icon="md-browsers">
                <i-table border stripe :columns="columns" :data="showList" style="min-width: 800px"></i-table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="dataTotal" :current="pageCurrent" @update:current="val => pageCurrent = val" :page-size="pageSize" @on-change="changePage" show-total show-elevator></Page>
                    </div>
                </div>
            </TabPane>
        </Tabs>
        <Drawer
            :title="this.drawTitle"
            v-model="drawerShow"
            width="560"
            :mask-closable="true"
            :styles="styles"
        >
            <Form ref="formData" :model="formData" :rules="ruleValidate">
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="" prop="TERMTYPECODE">
                            <Input v-model="formData.TERMTYPECODE"  :disabled="true" />
                        </FormItem>
                    </Col>
                    <Col span="12">
                        <FormItem label="" prop="TERMTYPENAME">
                            <Input v-model="formData.TERMTYPENAME"  :disabled="true" />
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="" prop="SYSCODE">
                            <Select v-model="formData.SYSCODE" placeholder="" clearable>
                                <i-option v-for="item in sysList" :value="item.SYSCODE" :key="ruleL">{{ item.SYSNAME }}</i-option>
                            </Select>
                        </FormItem>
                    </Col>
                    <Col span="12">
                        <FormItem label="()" prop="TERMINTERVAL">
                            <Input v-model="formData.TERMINTERVAL" />
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="()" prop="TERMOFFLINETIME">
                            <Input v-model="formData.TERMOFFLINETIME" />
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="" prop="ISHOST">
                            <RadioGroup v-model="formData.ISHOST">
                                <Radio value="1" label=""></Radio>
                                <Radio value="0" label=""></Radio>
                            </RadioGroup>
                        </FormItem>
                    </Col>
                    <Col span="12">
                        <FormItem label="" prop="ISMONITORALLDAY">
                            <RadioGroup v-model="formData.ISMONITORALLDAY" @on-change="allDayChange">
                                <Radio value="1" label=""></Radio>
                                <Radio value="0" label=""></Radio>
                            </RadioGroup>
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16" v-show="allDayShow">
                    <Col span="12">
                        <FormItem label="" prop="MONITORALLTIME">
                            <Time-picker confirm v-model="formData.MONITORALLTIME" format="HH:mm" type="timerange" placeholder="" style="width: 168px" @on-change="changeTime"></Time-picker>
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="24">
                        <FormItem label="" prop="MEMO">
                            <Input v-model="formData.MEMO" type="textarea" :autosize="{minRows: 2,maxRows: 2}" placeholder=""></Input>
                        </FormItem>
                    </Col>
                </Row>
             </Form>
             <div class="demo-drawer-footer">
                <Button type="error" style="margin-right: 8px" @click="drawerShow = false"></Button>
                <Button type="success" @click="editSubmit('formData')"></Button>
            </div>
        </Drawer>
        <Spin fix v-if="spinShow">
            <Icon type="ios-loading" size=30 class="demo-spin-icon-load"></Icon>
            <div style="font-size: 14px;padding-top: 15px"></div>
        </Spin>
    </div>
</template>
<script>
export default {
    data () {
        const validateNum = (rule, value, callback) => {
            const reg = /^([1-9]\d*|[0]{1,1})$/
            if (value === '') {
                callback()
            } else if (!reg.test(value)) {
                callback(new Error(''))
            } else {
                callback()
            }
        }
        return {
            delShow: false,
            spinShow: true,
            drawerShow: false,
            drawTitle: '',
            ruleList: [],
            allDayShow: true,
            styles: {
                height: 'calc(100% - 55px)',
                overflow: 'auto',
                paddingBottom: '53px',
                position: 'static'
            },
            formData: {
                TERMTYPECODE: '',
                TERMTYPENAME: '',
                SYSCODE: '',
                TERMINTERVAL: '',
                TERMOFFLINETIME: '',
                ISHOST: '',
                ISMONITORALLDAY: '',
                MONITORSTARTTIME: '',
                MONITORENDTIME: '',
                MONITORALLTIME: ['',''],
                MEMO: ''
            },
            ruleValidate: {
                TERMINTERVAL: [
                    { max: 5,message: '5', trigger: 'blur' },
                    { validator: validateNum, trigger: 'blur' }
                ],
                TERMOFFLINETIME: [
                    { max: 5,message: '5', trigger: 'blur' },
                    { validator: validateNum, trigger: 'blur' }
                ],
                MEMO: [
                    { max: 100,message: '100', trigger: 'blur' }
                ]
            },
            totalList: [],
            showList: [],
            searchList: [],
            dataTotal: 0,
            pageCurrent: 1,
            pageSize: 10,
            nameValue: '',
            columns: [
                {
                    title: '',
                    key: 'TERMTYPECODE'
                },
                {
                    title: '',
                    key: 'TERMTYPENAME'
                },
                {
                    title: '',
                    key: 'SYSNAME',
                    render: (h, params) => {
                        let text = ''
                        const _this = this
                        for (let i=0;i<_this.sysList.length;i++) {
                            if (_this.sysList[i].SYSCODE == params.row.SYSCODE) {
                                text = _this.sysList[i].SYSNAME
                            }
                        }
                        return h('div',text)
                    }
                },
                {
                    title: '()',
                    key: 'TERMINTERVAL'
                },
                {
                    title: '()',
                    key: 'TERMOFFLINETIME'
                },
                {
                    title: '',
                    key: 'ISHOST',
                    render (h, params) {
                        return h('div',params.row.ISVALID == 1 ? '' : '')
                    }
                },
                {
                    title: '',
                    key: 'ISMONITORALLDAY',
                    render (h, params) {
                        return h('div',params.row.ISMONITORALLDAY == 1 ? '' : '')
                    }
                },
                {
                    title: '',
                    key: 'MONITORSTARTTIME'
                },
                {
                    title: '',
                    key: 'MONITORENDTIME'
                },
                {
                    title: '',
                    key: 'MEMO'
                },
                {
                    title: '',
                    key: 'action',
                    width: 80,
                    align: 'center',
                    render: (h, params) => {
                        const self = this
                        return h('div',[
                            h('i-button',{
                                props: {
                                    type: 'warning',
                                    size: 'small'
                                },
                                on: {
                                    click: () => {
                                        self.edit(params.index)
                                    }
                                }
                            },'')
                        ])
                    }
                }
            ],
            sysList: [{"SYSCODE":"0","SYSNAME":""},{"SYSCODE":"1","SYSNAME":""},{"SYSCODE":"2","SYSNAME":""},{"SYSCODE":"3","SYSNAME":""},{"SYSCODE":"4","SYSNAME":""},{"SYSCODE":"5","SYSNAME":""},{"SYSCODE":"6","SYSNAME":""},{"SYSCODE":"7","SYSNAME":"POS"},{"SYSCODE":"8","SYSNAME":""},{"SYSCODE":"9","SYSNAME":""},{"SYSCODE":"10","SYSNAME":""},{"SYSCODE":"11","SYSNAME":""},{"SYSCODE":"12","SYSNAME":""},{"SYSCODE":"13","SYSNAME":""},{"SYSCODE":"14","SYSNAME":""},{"SYSCODE":"15","SYSNAME":""},{"SYSCODE":"16","SYSNAME":""},{"SYSCODE":"17","SYSNAME":""},{"SYSCODE":"18","SYSNAME":""},{"SYSCODE":"19","SYSNAME":""},{"SYSCODE":"20","SYSNAME":""},{"SYSCODE":"21","SYSNAME":""},{"SYSCODE":"22","SYSNAME":""},{"SYSCODE":"23","SYSNAME":""},{"SYSCODE":"24","SYSNAME":""},{"SYSCODE":"25","SYSNAME":""},{"SYSCODE":"26","SYSNAME":""},{"SYSCODE":"27","SYSNAME":""},{"SYSCODE":"28","SYSNAME":""},{"SYSCODE":"29","SYSNAME":""},{"SYSCODE":"30","SYSNAME":""},{"SYSCODE":"31","SYSNAME":""},{"SYSCODE":"32","SYSNAME":"BUS"},{"SYSCODE":"33","SYSNAME":""},{"SYSCODE":"34","SYSNAME":""},{"SYSCODE":"35","SYSNAME":""},{"SYSCODE":"36","SYSNAME":""},{"SYSCODE":"37","SYSNAME":""},{"SYSCODE":"38","SYSNAME":""},{"SYSCODE":"39","SYSNAME":""},{"SYSCODE":"40","SYSNAME":""},{"SYSCODE":"41","SYSNAME":""},{"SYSCODE":"42","SYSNAME":""},{"SYSCODE":"43","SYSNAME":""},{"SYSCODE":"44","SYSNAME":""},{"SYSCODE":"45","SYSNAME":""},{"SYSCODE":"46","SYSNAME":""},{"SYSCODE":"47","SYSNAME":""},{"SYSCODE":"48","SYSNAME":""},{"SYSCODE":"49","SYSNAME":""},{"SYSCODE":"50","SYSNAME":""},{"SYSCODE":"51","SYSNAME":""},{"SYSCODE":"52","SYSNAME":""},{"SYSCODE":"53","SYSNAME":""},{"SYSCODE":"54","SYSNAME":""},{"SYSCODE":"55","SYSNAME":""},{"SYSCODE":"56","SYSNAME":""},{"SYSCODE":"57","SYSNAME":""},{"SYSCODE":"58","SYSNAME":""},{"SYSCODE":"59","SYSNAME":"pos"},{"SYSCODE":"60","SYSNAME":""},{"SYSCODE":"200","SYSNAME":""}]
        }
    },
    methods: {
        edit (index) {
            const _showList = this.showList[index]
            const _formData = this.formData
            this.drawTitle = '' + _showList.TERMTYPENAME
            this.handleReset('formData')
            const arr = Object.keys(_formData)
            for (let i=0;i<arr.length;i++) {
                if (arr[i] != 'MONITORALLTIME') {
                    if (_showList[arr[i]] == '') {
                        _formData[arr[i]] = _showList[arr[i]].toString()
                    } else if (_showList[arr[i]] == null) {
                        _formData[arr[i]] = ''
                    } else {
                        _formData[arr[i]] = _showList[arr[i]].toString()
                    }
                }
            }
            _formData.MONITORALLTIME[0] = _showList.MONITORSTARTTIME
            _formData.MONITORALLTIME[1] = _showList.MONITORENDTIME
            _formData.ISHOST = _showList.ISHOST == 1 ? '' : ''
            _formData.ISMONITORALLDAY = _showList.ISMONITORALLDAY == 1 ? '' : ''
            if (_formData.ISMONITORALLDAY == '') {
                this.allDayShow = false
                _formData.MONITORALLTIME = ['','']
            }
            this.drawerShow = true
        },
        editSubmit (name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$ajax.get("editTermTypeNew.do",{
                            params: {
                                TERMTYPECODE: this.formData.TERMTYPECODE,
                                TERMTYPENAME: this.formData.TERMTYPENAME,
                                SYSCODE: this.formData.SYSCODE,
                                TERMINTERVAL: this.formData.TERMINTERVAL,
                                TERMOFFLINETIME: this.formData.TERMOFFLINETIME,
                                ISHOST: this.formData.ISHOST == '' ? 1 : 0,
                                ISMONITORALLDAY: this.formData.ISMONITORALLDAY == '' ? 1 : 0,
                                MONITORSTARTTIME: this.formData.MONITORALLTIME[0],
                                MONITORENDTIME: this.formData.MONITORALLTIME[1],
                                MEMO: this.formData.MEMO
                            }
                        })
                        .then(res => {
                            if (res.data.success == 'true') {
                                this.drawerShow = false
                                this.$Message.success('!')
                                this.spinShow = true
                                this.$ajax.get("listTermTypeNew.do")
                                    .then(res => {
                                        this.totalList = res.data
                                        this.searchList = this.totalList
                                        this.tableLoad()
                                        this.spinShow = false
                                    })
                                } else {
                                    this.$Message.error(res.data.msg)
                                }
                        })
                } else {
                    this.$Message.error('!')
                }
            })
        },
        handleReset (name) {
            this.$refs[name].resetFields();
        },
        changePage (index) {
            const _start = (index - 1) * this.pageSize
            const _end = index * this.pageSize
            this.showList = this.searchList.slice(_start, _end)
        },
        tableSearch () {
            this.searchList = this.totalList
            if (this.nameValue != '') {
                this.searchName()
            }
            this.tableLoad()
        },
        searchName () {
            let arr = []
            for (let i=0;i<this.searchList.length;i++) {
                if (this.searchList[i].TERMTYPENAME.indexOf(this.nameValue) >= 0) {
                    arr.push(this.searchList[i])
                }
            }
            this.searchList = arr
        },
        tableLoad () {
            this.dataTotal = this.searchList.length
            if (this.searchList < this.pageSize) {
                this.showList = this.searchList
            } else {
                this.showList = this.searchList.slice(0,this.pageSize)
            }
            this.pageCurrent = 1
            this.$emit('update:current', this.pageCurrent)
        },
        allDayChange () {
            this.formData.ISMONITORALLDAY == '' ? this.allDayShow = false : this.allDayShow = true
            this.formData.MONITORALLTIME = ['','']
        }
    },
    mounted() {
        this.$ajax.get("listTermTypeNew.do")
            .then(res => {
                this.totalList = res.data
                this.searchList = this.totalList
                this.tableLoad()
                this.spinShow = false
            })
    }
}
</script>



