                                <label></label><input :value="popDetail.detail.ACCEPTMAXNUMBER" readonly />
                            </Col>
                        </Row>
                        <Row :gutter="16">
                            <Col span="8">
                                <label></label><input :value="popDetail.detail.SUMMONEY" readonly />
                            </Col>
                            <Col span="8">
                                <label></label><input :value="popDetail.detail.PANELSPRINTED" readonly />
                            </Col>
                            <Col span="8">
                                <label></label><input :value="popDetail.detail.PANELSREMAINING" readonly />
                            </Col>
                        </Row>
                        <Row :gutter="16">
                            <Col span="8">
                                <label></label><input :value="popDetail.detail.STATUS" readonly />
                            </Col>
                            <Col span="8">
                                <label></label><input :value="popDetail.detail.PRINTERSTATUS" readonly />
                            </Col>
                            <Col span="8">
                                <label></label><input :value="popDetail.detail.LASTTRANTIME" readonly />
                            </Col>
                        </Row>
                        <Row :gutter="16">
                            <Col span="8">
                                <label></label><input :value="popDetail.detail.HINTNUMBER" readonly />
                            </Col>
                            <Col span="8">
                                <label></label><input :value="popDetail.detail.CACHNUMBER" readonly />
                            </Col>
                            <Col span="8">
                                <label></label><input :value="popDetail.detail.EXCEPTIONSTART" readonly />
                            </Col>
                        </Row>
                        <Row :gutter="16">
                            <Col span="8">
                                <label></label><input :value="popDetail.detail.ROLLCALLTIME" readonly />
                            </Col>
                        </Row>
                    </div>
                </TabPane>
                <TabPane label="" icon="md-stats" class=" pop-big" v-if="popResource" name="key1">
                    <i-table border stripe :columns="columnsResource" :data="popDetail.resource"></i-table>
                </TabPane>
                <TabPane label="" icon="md-filing" class="pop-big" v-if="popEdition" name="key2">
                    <i-table border stripe :columns="columnsEdition" :data="popDetail.edition"></i-table>
                </TabPane>
                <TabPane label="" icon="md-list" class="pop-big" v-if="popInfo" name="key3">
                    <i-table border stripe :columns="columnsInfo" :data="popDetail.info"></i-table>
                </TabPane>
                <TabPane label="" icon="md-list" class="pop-big" v-if="popSubsystem" name="key4">
                    <i-table border stripe :columns="columnsSubsystem" :data="popDetail.subsystem"></i-table>
                </TabPane>
                <TabPane label="Oracle" icon="md-list" class="pop-detail pop-oracle pop-big" v-if="popOracle" name="key5">
                    <Row>
                        <Col span="8">
                            <label></label><input :value="popDetail.oracle.ROLLCALLTIME" readonly />
                        </Col>
                        <Col span="8">
                            <label></label><input :value="popDetail.oracle.MAXCONNCOUNT" readonly />
                        </Col>
                        <Col span="8">
                            <label></label><input :value="popDetail.oracle.CURRCONNCOUNT" readonly />
                        </Col>
                    </Row>
                    <Row>
                        <Col span="8">
                            <label></label><input :value="popDetail.oracle.DBSTATE" readonly />
                        </Col>
                        <Col span="8">
                            <label></label><input :value="popDetail.oracle.SHAREPOOLSIZE" readonly />
                        </Col>
                        <Col span="8">
                            <label></label><input :value="popDetail.oracle.SPUSEDPENCENT" readonly />
                        </Col>
                    </Row>
                    <Row>
                        <Col span="8">
                            <label>SGA</label><input :value="popDetail.oracle.SGAHITRATIO" readonly />
                        </Col>
                        <Col span="8">
                            <label>SGA</label><input :value="popDetail.oracle.LIBRARYCACHEHITRATIO" readonly />
                        </Col>
                        <Col span="8">
                            <label>SGA</label><input :value="popDetail.oracle.ROWCACHEHITRATIO" readonly />
                        </Col>
                    </Row>
                    <Row>
                        <Col span="8">
                            <label></label><input :value="popDetail.oracle.INVALIDOBJECT" readonly />
                        </Col>
                        <Col span="8">
                            <label></label><input :value="popDetail.oracle.MEMORYDISKSORTSRATIO" readonly />
                        </Col>
                        <Col span="8">
                            <label></label><input :value="popDetail.oracle.UPDATETIME" readonly />
                        </Col>
                    </Row>
                </TabPane>
            </Tabs>
             <div class="demo-drawer-footer">
                <Button type="error" style="margin-right: 8px" @click="drawerShow = false"></Button>
            </div>
        </Drawer>
        <Spin fix v-if="spinShow">
            <Icon type="ios-loading" size=30 class="demo-spin-icon-load"></Icon>
            <div style="font-size: 14px;padding-top: 15px"></div>
        </Spin>
    </div>
</template>
<script>
export default {
    data () {
        return {
            typeList: '',
            typeValue: '%',
            tabKey: 'key0',
            popDetail: {detail: {},oracle: {}},
            drawerShow: false,
            popResource: false,
            popEdition: false,
            popSubsystem: false,
            popOracle: false,
            popInfo: false,
            spinShow: true,
            totalList: [],
            showList: [],
            searchList: [],
            dataTotal: 0,
            pageCurrent: 1,
            pageSize: 10,
            nameValue: '',
            statusList: [
                {
                    value: '%',
                    label: ''
                },
                {
                    value: '1',
                    label: ''
                },
                {
                    value: '2',
                    label: ''
                },
                {
                    value: '3',
                    label: ''
                },
                {
                    value: '0',
                    label: ''
                }
            ],
            selectStatus: '%',
            columnsResource: [
                {
                    title: '',
                    key: 'key_0'
                },
                {
                    title: '',
                    key: 'key_1'
                },
                {
                    title: '',
                    key: 'key_2'
                },
                {
                    title: 'CPU',
                    key: 'key_3'
                },
                {
                    title: '',
                    key: 'key_4'
                },
                {
                    title: '',
                    key: 'key_5'
                }
            ],
            columnsEdition: [
                {
                    title: '',
                    key: 'key_0'
                },
                {
                    title: '',
                    key: 'key_1'
                },
                {
                    title: '',
                    key: 'key_2'
                },
                {
                    title: '',
                    key: 'key_3'
                }
            ],
            columnsInfo: [
                {
                    title: '',
                    key: 'key_0'
                },
                {
                    title: ' ID',
                    key: 'key_1'
                },
                {
                    title: '',
                    key: 'key_2'
                },
                {
                    title: '',
                    key: 'key_3'
                },
                {
                    title: '',
                    key: 'key_4'
                },
                {
                    title: '',
                    key: 'key_5'
                },
            ],
            columnsSubsystem: [
                {
                    title: '',
                    key: 'key_0'
                },
                {
                    title: '',
                    key: 'key_1'
                }
            ],
            columns: [
                {
                    title: '',
                    key: 'TERMNAME',
                    render (h, params) {
                        return h('div',params.row.TERMNAME)
                    }
                },
                {
                    title: '',
                    key: 'TERMNO',
                    
                },
                {
                    title: '',
                    key: 'RUNSTATUS',
                    render: (h, params) => {
                        let color = params.row.RUNSTATUS == 1 ? 'green' : params.row.RUNSTATUS == 2 ? 'red' : params.row.RUNSTATUS == 3 ? 'blue' : 'gray'
                        let text = params.row.RUNSTATUS == 1 ? '' : params.row.RUNSTATUS == 2 ? '' : params.row.RUNSTATUS == 3 ? '' : ''
                        return h('tag',{
                            props: {
                                color: color
                            }
                        },text)
                    }
                },
                {
                    title: '',
                    key: 'PARENTTERMNAME'
                },
                {
                    title: '',
                    key: 'EXCEPTIONSTART'
                },
                {
                    title: '',
                    key: 'ROLLCALLTIME'
                },
                {
                    title: 'IP',
                    key: 'TERMIP'
                },
                {
                    title: '',
                    key: 'TERMPORT'
                },
                {
                    title: '',
                    key: 'action',
                    width: 80,
                    align: 'center',
                    render: (h, params) => {
                        const self = this
                        return h('div',[
                            h('i-button',{
                                props: {
                                    type: 'primary',
                                    size: 'small'
                                },
                                attrs: {
                                    style: "margin-right: 10px;"
                                },
                                on: {
                                    click: () => {
                                        self.show(params.index,self.showList[params.index].ALERTTYPECODE)
                                    }
                                }
                            },'')
                        ])
                    }
                }
            ]
        }
    },
    methods: {
        show (index,type) {
            const _this = this
            _this.spinShow = true
            _this.$ajax.get("newgetTermInfo.do",{
                params: {
                    no: _this.showList[index].TERMNO
                }
            })
            .then(res => {
                _this.tabKey = 'key0'
                _this.popDetail = res.data
                const popShow = document.getElementsByClassName("pop-show")
                for (let i=0;i<popShow.length;i++) {
                    popShow[i].style.display="none"
                }
                switch(type) {
                    //
                    case '0':
                        document.getElementsByClassName("pop-show-0")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //
                    case '1':
                        document.getElementsByClassName("pop-show-1")[0].style.display="block"
                        _this.popResource = true
                        _this.popEdition = true
                        _this.popInfo = true
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //
                    case '2':
                        document.getElementsByClassName("pop-show-2")[0].style.display="block"
                        _this.popResource = true
                        _this.popEdition = true
                        _this.popInfo = true
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //
                    case '3':
                        document.getElementsByClassName("pop-show-3")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //
                    case '4':
                        document.getElementsByClassName("pop-show-4")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = true
                        _this.popOracle = false
                        break
                    //
                    case '5':
                        document.getElementsByClassName("pop-show-5")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = true
                        break
                    //
                    case '6':
                        document.getElementsByClassName("pop-show-6")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = true
                        _this.popOracle = false
                        break
                    //POS
                    case '7':
                        document.getElementsByClassName("pop-show-7")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //
                    case '8':
                        document.getElementsByClassName("pop-show-8")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //
                    case '9':
                        document.getElementsByClassName("pop-show-9")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //
                    case '10':
                        document.getElementsByClassName("pop-show-10")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //
                    case '11':
                        document.getElementsByClassName("pop-show-11")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //
                    case '12':
                        document.getElementsByClassName("pop-show-12")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    default:
                        document.getElementsByClassName("pop-show-0")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                }
                this.spinShow = false
                this.drawerShow = true
            })
        },
        changePage (index) {
            const _start = (index - 1) * this.pageSize
            const _end = index * this.pageSize
            this.showList = this.searchList.slice(_start, _end)
        },
        tableSearch () {
            this.searchList = this.totalList
            if (this.selectStatus != '%') {
                this.searchStatus()
            }
            if (this.nameValue != '') {
                this.searchName()
            }
            if (this.typeValue != '%') {
                this.searchType()
            }
            this.tableLoad()
        },
        searchName () {
            let arr = []
            for (let i=0;i<this.searchList.length;i++) {
                if (this.searchList[i].TERMNAME.indexOf(this.nameValue) >= 0) {
                    arr.push(this.searchList[i])
                }
            }
            this.searchList = arr
        },
        searchStatus () {
            let arr = []
            for (let i=0;i<this.searchList.length;i++) {
                if (this.searchList[i].RUNSTATUS == this.selectStatus) {
                    arr.push(this.searchList[i])
                }
            }
            this.searchList = arr
        },
        searchType () {
            let arr = []
            for (let i=0;i<this.searchList.length;i++) {
                if (this.searchList[i].TERMTYPECODE == this.typeValue) {
                    arr.push(this.searchList[i])
                }
            }
            this.searchList = arr
        },
        tableLoad () {
            this.dataTotal = this.searchList.length
            if (this.searchList < this.pageSize) {
                this.showList = this.searchList
            } else {
                this.showList = this.searchList.slice(0,this.pageSize)
            }
            this.pageCurrent = 1
            this.$emit('update:current', this.pageCurrent)
        }
    },
    mounted() {
        this.$ajax.get("newlistTermStatus.do")
            .then(res => {
                this.totalList = res.data
                this.searchList = this.totalList
                this.tableLoad()
                this.spinShow = false
            })
        this.$ajax.get("getTermTypeListNew.do")
            .then(res => {
                this.typeList = res.data
            })
    }
}
</script>



