<template>
    <div>
        <div class="search-con">
            <span></span>
            <date-picker v-model="dateValue" :options="dataOptions" type="daterange" format="yyyy-MM-dd" placeholder="" style="width: 200px;margin-right: 8px;" placement="bottom-end" :editable="false"></date-picker>
            <span></span>
            <i-select v-model="selectStatus" style="width: 150px;margin-right: 8px;" placeholder="" clearable>
                <i-option value="0"></i-option>
                <i-option value="1"></i-option>
            </i-select>
            <i-button @click="tableSearch" type="primary" icon="md-search"></i-button>
        </div>
        <Tabs>
            <TabPane label="" icon="md-browsers">
                <i-table border stripe :columns="columns" :data="showList" style="min-width: 800px"></i-table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="dataTotal" :current="pageCurrent" @update:current="val => pageCurrent = val" :page-size="pageSize" @on-change="changePage" show-total show-elevator></Page>
                    </div>
                </div>
            </TabPane>
        </Tabs>
        <Spin fix v-if="spinShow">
            <Icon type="ios-loading" size=30 class="demo-spin-icon-load"></Icon>
            <div style="font-size: 14px;padding-top: 15px"></div>
        </Spin>
    </div>
</template>
<script>
export default {
    data () {
        return {
            spinShow: true,
            self: this,
            dateValue: '',
            drawer: false,
            styles: {
                height: 'calc(100% - 55px)',
                overflow: 'auto',
                paddingBottom: '53px',
                position: 'static'
            },
            totalList: [],
            showList: [],
            searchList: [],
            dataTotal: 0,
            pageCurrent: 1,
            pageSize: 10,
            selectStatus: '',
            columns: [
                {
                    title: '',
                    key: 'TERMNO'
                },
                {
                    title: '',
                    key: 'EXCEPTIONDESC'
                },
                {
                    title: '',
                    key: 'EXCEPTIONSTATE',
                    render: (h, params) => {
                        return h('div',params.row.EXCEPTIONSTATE == 1 ? '' : params.row.EXCEPTIONSTATE == 2 ? '' : params.row.EXCEPTIONSTATE == 3 ? '' : '')
                    }
                },
                {
                    title: '',
                    key: 'EXCEPTIONSTART'
                },
                {
                    title: '',
                    key: 'ALARMTIME'
                },
                {
                    title: '',
                    key: 'EXCEPTIONEND'
                },
                {
                    title: '',
                    key: 'ALARMMODE',
                    render: (h, params) => {
                        return h('div',params.row.ALARMMODE == 1 ? '' : params.row.ALARMMODE == 2 ? '' : params.row.ALARMMODE == 3 ? '' : '')
                    }
                },
                {
                    title: '',
                    key: 'ALARMSTUTAS',
                    render: (h, params) => {
                        return h('div',params.row.ALARMSTUTAS == 1 ? '' : '')
                    }
                },
                {
                    title: '',
                    key: 'ALARMMEMO'
                }
            ],
            dataOptions: {
                disabledDate (date) {
                    return date && date.valueOf() > Date.now();
                }
            }
        }
    },
    methods: {
        changePage (index) {
            const _start = (index - 1) * this.pageSize
            const _end = index * this.pageSize
            this.showList = this.searchList.slice(_start, _end)
        },
        padDate (date) {
            let year = date.getFullYear()
            const m = (date.getMonth() + 1)
            let month = m < 10 ? '0' + m : m
            let day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate()
            return year + '-' + month + '-' + day
        },
        ajaxData (paramsData) {
            const selfError = this
            this.spinShow = true
            this.$ajax.get("getAlarmLogNew.do",{
                    params: paramsData
                })
                .then(res => {
                    this.totalList = res.data
                    this.searchList = this.totalList
                    this.tableLoad()
                    this.spinShow = false
                })
                .catch(function (error) {
                    const str = error + ''
                    if (str.search('timeout') !== -1) {
                        selfError.$Message.error('')
                        selfError.spinShow = false
                    }
                })
        },
        tableSearch () {
            const paramsData = {
                startdate: this.padDate(this.dateValue[0]),
                enddate: this.padDate(this.dateValue[1]),
                alarmstatus: this.selectStatus
            }
            this.ajaxData(paramsData)
        },
        tableLoad () {
            this.dataTotal = this.searchList.length
            if (this.searchList < this.pageSize) {
                this.showList = this.searchList
            } else {
                this.showList = this.searchList.slice(0,this.pageSize)
            }
            this.pageCurrent = 1
            this.$emit('update:current', this.pageCurrent)
        }
    },
    mounted() {
        const today = new Date()
        const lastSevenDay = new Date(today.getTime() - 6 * 24 * 3600 * 1000)
        this.dateValue = [lastSevenDay,today]
        const paramsData = {
            startdate: this.padDate(this.dateValue[0]),
            enddate: this.padDate(this.dateValue[1])
        }
        this.ajaxData(paramsData)
    }
}
</script>



